@charset "UTF-8";
body,
button,
input,
select,
textarea {
  font-family: "PTSans-Regular";
  color: #496e86;
  font-size: 16px; }

.display-flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.logo-link {
  width: 359px;
  height: 82px;
  background-image: url(../images/background/logo.png);
  background-position: center;
  background-size: cover; }
  .logo-link img {
    display: none; }

.meta {
  display: flex;
  justify-content: space-around;
  padding: 14px 0;
  font-family: "FontAwesome";
  color: #7bbfec;
  font-size: 14px; }
  .meta-views:before {
    content: "";
    display: inline-block;
    font-family: "FontAwesome";
    color: #7bbfec;
    font-size: 14px; }
  .meta-messag:before {
    content: "";
    display: inline-block;
    font-family: "FontAwesome";
    color: #7bbfec;
    font-size: 14px; }
  .meta.white {
    color: #ffffff;
    justify-content: flex-start; }
    .meta.white .meta-messag {
      padding: 0 20px; }

.soclink {
  font-family: "FontAwesome";
  color: #ffffff;
  font-size: 16px; }

.title-articl {
  width: 100%;
  font-family: "PTSerif-Bold";
  color: #496e86;
  font-size: 20px; }
  .title-articl:after {
    content: "";
    display: block;
    width: 20px;
    border: 1px solid #ffffff;
    margin-top: 16px;
    margin-bottom: 16px; }
  .title-articl.right {
    display: inline-block;
    padding: 30px 0 0 70px; }
  .title-articl:after {
    border-color: #7bbfec; }

.search-form {
  display: flex;
  width: 290px;
  flex-wrap: wrap; }
  .search-form .searchstring {
    display: inline-block;
    width: calc(100% - 55px);
    height: 36px;
    padding-left: 10px;
    border: 1px solid #eff8fd;
    background-color: #eff8fd;
    border-radius: 0;
    font-family: "PTSans-Regular";
    color: rgba(73, 110, 134, 0.5);
    font-size: 14px; }
    .search-form .searchstring::-moz-placeholder {
      font-family: "PTSans-Regular";
      color: rgba(73, 110, 134, 0.5);
      font-size: 14px;
      opacity: 1; }
    .search-form .searchstring::-webkit-input-placeholder {
      font-family: "PTSans-Regular";
      color: rgba(73, 110, 134, 0.5);
      font-size: 14px;
      opacity: 1; }

.searchsubmit {
  display: inline-block;
  width: 40px;
  height: 40px;
  font-family: "FontAwesome";
  color: #ffffff;
  font-size: 14px;
  vertical-align: top;
  border: 1px solid #eff8fd;
  background-color: #7cc0ed;
  border-radius: 0;
  cursor: pointer; }

.sign-search {
  font-family: "PTSans-Regular";
  color: #ffffff;
  font-size: 12px;
  padding-top: 5px; }

header {
  background-color: #80b9e6;
  background-image: url(../images/background/image_heder.jpg);
  background-position: center;
  background-size: cover; }

.wrapper-header {
  display: flex;
  flex-wrap: wrap;
  width: 1060px;
  margin: 50px auto;
  margin: 0 auto; }

.header-body {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 37px 0; }
  .header-body .menu-header-top {
    display: flex; }
    .header-body .menu-header-top ul.menu-top {
      padding: 0 20px; }
      .header-body .menu-header-top ul.menu-top a {
        display: inline-block;
        font-family: "PTSans-Regular";
        color: #ffffff;
        font-size: 14px;
        padding-bottom: 5px;
        transition: 1s;
        position: relative; }
        .header-body .menu-header-top ul.menu-top a:after {
          content: '';
          position: absolute;
          top: 100%;
          left: 0;
          width: 100%;
          height: 1px;
          background: #ffffff;
          opacity: 0;
          transition: opacity .3s,transform .3s;
          transform: translateY(10px); }
        .header-body .menu-header-top ul.menu-top a:hover:after {
          opacity: 1;
          transform: translateY(-4px); }

.main-navigation {
  width: 100%;
  background-image: url(../images/background/pattern_blue.png);
  background-position: center;
  background-size: cover; }
  .main-navigation ul.menu {
    display: flex;
    width: 1060px;
    margin: 0 auto; }
    .main-navigation ul.menu li.menu-item {
      transition: all 0.3s;
      position: relative; }
      .main-navigation ul.menu li.menu-item:hover .sub-menu {
        display: block; }
      .main-navigation ul.menu li.menu-item:before {
        content: '•';
        display: inline-block;
        vertical-align: middle;
        font-family: "PTSans-Regular";
        color: #ffffff;
        font-size: 12px; }
      .main-navigation ul.menu li.menu-item:hover:before {
        color: transparent; }
      .main-navigation ul.menu li.menu-item:hover + li:before {
        color: transparent; }
      .main-navigation ul.menu li.menu-item:hover {
        background-color: #7cc0ed; }
      .main-navigation ul.menu li.menu-item:first-of-type:before {
        content: none; }
    .main-navigation ul.menu a {
      display: inline-block;
      font-family: "PTSans-Regular";
      color: #ffffff;
      font-size: 16px;
      padding: 18px 10px; }
    .main-navigation ul.menu ul.sub-menu {
      display: none;
      background-color: #7cc0ed;
      position: absolute;
      top: 100%;
      margin: 0;
      z-index: 2; }
      .main-navigation ul.menu ul.sub-menu .menu-item:hover {
        background-color: #2f8fc8; }
      .main-navigation ul.menu ul.sub-menu .menu-item::before {
        font-size: 0; }

