footer {
  background-color: #3e95c9; }

.wrapper-footer {
  display: flex;
  justify-content: space-between;
  padding: 25px 0;
  width: 1060px;
  margin: 50px auto;
  margin: 0 auto; }

.menu-footer {
  display: flex; }
  .menu-footer ul {
    list-style: none;
    padding: 0 30px;
    margin: 0; }
    .menu-footer ul a {
      display: inline-block;
      font-family: "PTSans-Regular";
      color: #ffffff;
      font-size: 16px;
      padding-bottom: 7px;
      transition: 1s;
      position: relative; }
      .menu-footer ul a:after {
        content: '';
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        height: 2px;
        background: #ffffff;
        opacity: 0;
        transition: opacity .3s,transform .3s;
        transform: translateY(10px); }
